import { ref, onMounted, watch } from "vue";

export function useFavorites() {
  const favorites = ref([]);
  const storageKey = "uipc-menu-favorites";

  const loadFavorites = () => {
    const storedFavorites = localStorage.getItem(storageKey);
    if (storedFavorites) {
      favorites.value = JSON.parse(storedFavorites);
    }
  };

  const saveFavorites = () => {
    localStorage.setItem(storageKey, JSON.stringify(favorites.value));
  };

  const addFavorite = (item) => {
    if (item && item.url && !favorites.value.some((fav) => fav.url === item.url)) {
      favorites.value.push(item);
    }
  };

  const removeFavorite = (item) => {
    const index = favorites.value.findIndex((fav) => fav.url === item.url);
    if (index > -1) {
      favorites.value.splice(index, 1);
    }
  };

  const isFavorite = (url) => {
    return favorites.value.some((fav) => fav.url === url);
  };

  onMounted(() => {
    loadFavorites();
  });

  watch(
    favorites,
    () => {
      saveFavorites();
    },
    { deep: true }
  );

  return {
    favorites,
    addFavorite,
    removeFavorite,
    isFavorite,
  };
}
