<script setup>
import { ref, computed, watchEffect, defineProps, watch, nextTick } from "vue";

// Funcs
import { encodeToHash } from "@/assets/js/functions/encodeToHash.js";
import { lmnFetch } from "@/assets/js/functions/lmnFetch.js";
import { inSearch } from "@/assets/js/functions/inSearch.js";
import { processMenu } from "./src/processMenu.js";
import { returnOriginalLinkAttribute } from "./src/returnOriginalLinkAttribute.js";
import { returnIconOverrides } from "./src/returnIconOverrides.js";
import { returnDashIconClasses } from "./src/returnDashIconClasses.js";
import { setupColorScheme, useColorScheme } from "@/assets/js/functions/useColorScheme.js";
import { VueDraggableNext } from "vue-draggable-next";
const { prefersDark } = useColorScheme();

// Comps
import AppIcon from "@/components/utility/icons/index.vue";
import AppButton from "@/components/utility/app-button/index.vue";
import AppInput from "@/components/utility/text-input/index.vue";
import ResizerHandler from "@/components/app/panel-resizer/index.vue";
import SubMenu from "./src/submenu.vue";
import MenuIcon from "./src/menu-icon.vue";
import MenuItemName from "./src/menu-item-name.vue";
import MenuItemLink from "./src/menu-item-link.vue";
import SubMenuItem from "./src/submenu-item.vue";

// Store
import { useAppStore } from "@/store/app/app.js";
const appStore = useAppStore();

// Refs
import { loading, menu, dashIconsList, OGmenu } from "./src/constants.js";
const gravatar = ref(false);
const search = ref("");
const activeIndex = ref(-1);
const menupanel = ref(null);
const panelWidth = ref(240);
const menus = ref([]);
const props = defineProps(["mobile"]);

// Favorites
import { useFavorites } from "./src/useFavorites.js";
const { favorites, addFavorite, removeFavorite, isFavorite } = useFavorites();

/**
 * Computed property that returns a flat index for menu items, excluding separators
 * @returns {Array} Flattened array of menu items without separators.
 */
const flattenedSearchResults = computed(() => {
  const flattened = [];
  returnMenuItems.value.forEach((item) => {
    if (item.type !== "separator") {
      flattened.push({ ...item, isTopLevel: true });
      if (item.submenu && item.submenu.length) {
        searchedSubs(item.submenu).forEach((subItem) => {
          if (subItem.type !== "separator") {
            flattened.push({ ...subItem, isTopLevel: false, parentName: item.name });
          }
        });
      }
    }
  });
  return flattened;
});

/**
 * Computed property that filters menu items based on the search value.
 * @returns {Array} Filtered array of menu items.
 */
const returnMenuItems = computed(() => {
  if (!Array.isArray(menu.value)) return [];
  return menu.value.filter((item) => (inSearch(search.value, item.name, item.url) || inSearch(search.value, JSON.stringify(item.submenu))) && !item.settings?.hidden);
});

/**
 * Computed property that returns whether the menu is minimised
 */
const isMenuMinified = computed(() => {
  if (props.mobile) return false;
  return appStore.state.menu_minimised;
});

/**
 * Computed property that filters sub menu items based on the search value.
 * @returns {Array} Filtered array of menu items.
 */
const searchedSubs = (submenu) => {
  return submenu.filter((item) => inSearch(search.value, item.name, item.url) && !item.settings?.hidden);
};

const getMenuForUser = async () => {
  // Ensure the original menu has been parsed before moving on
  await setMenu();
  setTimeout(setMenu, 1000);

  // Attempt to fetch menu from cache
  const cachedMenu = getMenuFromLocalStorage();
  if (Array.isArray(cachedMenu)) {
    menu.value = cachedMenu;
    filterMenu();
    return;
  } else if (cachedMenu == "no_menus") {
    menu.value = [...OGmenu.value];
    return;
  }

  setTimeout(getRemoteMenus, 1);
};

const returnLayout = computed(() => {
  return appStore.state.uixpress_settings?.layout;
});

const getRemoteMenus = async () => {
  // Ensure the original menu has been parsed before moving on
  await setMenu();

  // Attempt to fetch menu from cache
  const cachedMenu = getMenuFromLocalStorage();
  if (Array.isArray(cachedMenu)) {
    menu.value = cachedMenu;
    filterMenu();
    return;
  } else if (cachedMenu == "no_menus") {
    menu.value = [...OGmenu.value];
    return;
  }

  loading.value = true;

  const args = { endpoint: "wp/v2/uipxmenus", params: { per_page: 100, status: "publish" } };
  const data = await lmnFetch(args);

  loading.value = false;

  if (!data) {
    menu.value = [...OGmenu.value];
    return;
  }

  menus.value = data.data;

  setActiveMenu();
};

const setActiveMenu = () => {
  // No custom menus so bail
  if (!menus.value.length) {
    menu.value = [...OGmenu.value];
    cacheMenu("no_menus");
    return;
  }

  for (let remoteMenu of menus.value) {
    const activeForEveryone = remoteMenu.meta?.menu_settings?.applies_to_everyone;

    // Menu is set to be active for everyone
    if (activeForEveryone && Array.isArray(remoteMenu.meta?.menu_items)) {
      // Do not set empty menus to active to avoid lock out
      if (!remoteMenu.meta?.menu_items.length) continue;

      // Update menu
      menu.value = [...remoteMenu.meta?.menu_items];
      cacheMenu(remoteMenu.meta?.menu_items);
      filterMenu();
      return;
    }

    const userRoles = appStore.state.userRoles;
    const includesRoles = remoteMenu.meta?.menu_settings?.includes ? remoteMenu.meta?.menu_settings?.includes.filter((item) => item.type == "role") : [];
    const includesUsers = remoteMenu.meta?.menu_settings?.includes ? remoteMenu.meta?.menu_settings?.includes.filter((item) => item.type == "user") : [];

    // Check if user is matched
    const matchedUser = includesUsers.find((item) => item.id == appStore.state.userID);

    // Check if user role matches
    let matchedRole = false;
    for (let role of userRoles) {
      const match = includesRoles.find((item) => item.value == role);
      if (match) {
        matchedRole = true;
        break;
      }
    }

    const excludedRoles = remoteMenu.meta?.menu_settings?.excludes ? remoteMenu.meta?.menu_settings?.excludes.filter((item) => item.type == "role") : [];
    const excludedUsers = remoteMenu.meta?.menu_settings?.excludes ? remoteMenu.meta?.menu_settings?.excludes.filter((item) => item.type == "user") : [];

    // Check if user is excluded
    const matchedExcludedUser = excludedUsers.find((item) => item.id == appStore.state.userID);

    // Check if user role matches
    let matchedExcludedRole = false;
    for (let role of userRoles) {
      const match = excludedRoles.find((item) => item.value == role);
      if (match) {
        matchedExcludedRole = true;
        break;
      }
    }

    // User is either matched by role or user id and not excluded by role or user id
    if ((matchedUser || matchedRole) && !matchedExcludedUser && !matchedExcludedRole) {
      // Update menu
      menu.value = [...remoteMenu.meta?.menu_items];
      cacheMenu(remoteMenu.meta?.menu_items);
      filterMenu();
      return;
    }
  }
  // Menus applied so set default
  menu.value = [...OGmenu.value];
  cacheMenu("no_menus");
};

/**
 * Saves the menu into local storage
 */
const cacheMenu = (menu) => {
  const key = `uixpress_menu_${appStore.state.userID}`;
  const item = {
    value: menu,
    timestamp: new Date().getTime(),
  };
  localStorage.setItem(key, JSON.stringify(item));
};

// Function to fetch array from localStorage, return null if expired
function getMenuFromLocalStorage() {
  const key = `uixpress_menu_${appStore.state.userID}`;
  const item = localStorage.getItem(key);

  if (!item) {
    return null;
  }

  const parsedItem = JSON.parse(item);
  const now = new Date().getTime();
  const oneHour = 60 * 60 * 1000; // 1 hour in milliseconds

  if (now - parsedItem.timestamp > oneHour) {
    localStorage.removeItem(key);
    return null;
  }

  return parsedItem.value;
}

/**
 * Removes items that no longer exist and adds new items
 *
 * @since 1.0.9
 */
const filterMenu = () => {
  let activeItem = document.querySelector("#adminmenu a.current") || document.querySelector("#adminmenu .wp-menu-open a") || document.querySelector("#adminmenu a[aria-current='page']");
  let activeURL = "";

  // Get active items URL
  if (activeItem) {
    if (activeItem.tagName && activeItem.tagName.toLowerCase() === "a") {
      activeURL = activeItem.getAttribute("href");
    }
  }

  // Remove items that no longer exist
  menu.value = menu.value.filter((item) => checkIfItemExists(item.id) || item.custom).map((item) => ({ ...item, active: activeURL == item.url }));

  // Remove sub items that no longer exist
  for (let toplevel of menu.value) {
    // Bail if it's a sep
    if (toplevel.type == "separator" || !Array.isArray(toplevel.submenu)) continue;
    toplevel.submenu = toplevel.submenu.filter((item) => checkIfItemExists(item.id) || item.custom).map((item) => ({ ...item, active: activeURL == item.url }));
  }

  // Check for items that are new and push to the menu
  for (let [index, toplevel] of OGmenu.value.entries()) {
    // Push to the menu if it's new
    if (!isItemInCustomMenu(toplevel.id)) {
      menu.value.splice(index, 0, toplevel);
    }

    // Continue if there is no submenu
    if (!toplevel.submenu) continue;

    // Loop submenu items to check for new items
    for (let [subindex, sub] of toplevel.submenu.entries()) {
      // Item exists in the menu so continue
      if (isItemInCustomMenu(sub.id)) continue;

      // Find correct top level item to push it to
      const parent = menu.value.find((item) => item.id == toplevel.id);

      if (parent) {
        parent.submenu.splice(subindex, 0, sub);
      } else {
        toplevel.submenu = [sub];
        menu.value.splice(index, 0, toplevel);
      }
    }
  }
};

const isItemInCustomMenu = (id) => {
  const existingTopLevel = menu.value.find((item) => item.id == id);
  const existingSubLevel = flattenedCustomMenuSubItems.value.find((item) => item.id == id);
  return existingTopLevel || existingSubLevel;
};

const checkIfItemExists = (id) => {
  const existingTopLevel = OGmenu.value.find((item) => item.id == id);
  const existingSubLevel = flattenedSubItems.value.find((item) => item.id == id);
  return existingTopLevel || existingSubLevel;
};

/**
 * Checks if a given link is active.
 * @param {Object} link - The link object to check.
 * @returns {boolean} True if the link is active, false otherwise.
 */

const isActive = (link) => {
  if (link.active) return true;

  if (link.submenu) {
    return link.submenu.find((item) => item.active);
  }
};

/**
 * Sets the user's gravatar based on their email.
 * @async
 */
const setGravatar = async () => {
  const hashHex = await encodeToHash(appStore.state.userEmail);
  gravatar.value = `https://gravatar.com/avatar/${hashHex}?d=blank`;
};

/**
 * Handles the down arrow key press event.
 * Increments the active index, wrapping around to the start if it reaches the end.
 */
const handleArrowDown = () => {
  activeIndex.value = (activeIndex.value + 1) % flattenedSearchResults.value.length;
};

/**
 * Handles the up arrow key press event.
 * Decrements the active index, wrapping around to the end if it reaches the start.
 */
const handleArrowUp = () => {
  activeIndex.value = (activeIndex.value - 1 + flattenedSearchResults.value.length) % flattenedSearchResults.value.length;
};

/**
 * Handles the enter key press event.
 * Navigates to the URL of the currently selected search result item.
 */
const handleKeyEnter = () => {
  if (activeIndex.value >= 0 && activeIndex.value < flattenedSearchResults.value.length) {
    const selectedItem = flattenedSearchResults.value[activeIndex.value];
    window.location.href = selectedItem.url;
  }
};

/**
 * Checks if a given link is currently focused.
 * @param {Object} link - The link object to check.
 * @param {string} link.url - The URL of the link.
 * @param {string} link.name - The name of the link.
 * @returns {boolean|undefined} True if the link is focused, undefined otherwise.
 */
const isFocused = (link) => {
  if (!search.value) return;
  const selectedItem = flattenedSearchResults.value[activeIndex.value];
  // Nothing found so bail
  if (!selectedItem) return;
  // Check if item matches current index
  if (selectedItem.url == link.url && selectedItem.name == link.name) return true;
};

/**
 * Updates the width of the panel.
 * @param {number} newWidth - The new width to set for the panel.
 */
const updatePanelWidth = (newWidth) => {
  panelWidth.value = newWidth;
};

/**
 * Sets the menu
 */
const setMenu = async () => {
  const menuNode = document.querySelector("#adminmenumain");
  const { processedMenu } = await processMenu(menuNode);

  OGmenu.value = processedMenu;
};

const flattenedCustomMenuSubItems = computed(() => {
  return menu.value.reduce((acc, item) => {
    if (Array.isArray(item.submenu)) {
      acc.push(...item.submenu);
    }
    return acc;
  }, []);
});

const flattenedSubItems = computed(() => {
  return OGmenu.value.reduce((acc, item) => {
    if (Array.isArray(item.submenu)) {
      acc.push(...item.submenu);
    }
    return acc;
  }, []);
});

const shouldShowSubMenu = (link) => {
  // No submenu
  if (!Array.isArray(link.submenu)) return false;
  // Empty submenu
  if (!link.submenu.length) return false;
  // Link is active
  if (isActive(link) || link.active) return true;
  // Search is active
  if (search.value && searchedSubs(link.submenu).length) return true;

  return false;
};

/**
 * Watches for changes in activeIndex and flattenedSearchResults.
 * Ensures activeIndex stays within valid bounds.
 */
watchEffect(() => {
  if (activeIndex.value > flattenedSearchResults.value.length - 1) activeIndex.value = 0;
  if (activeIndex.value < 0) activeIndex.value = flattenedSearchResults.value.length - 1;
});

watchEffect(() => {
  document.documentElement.style.setProperty("--uixp-menu-width", `${panelWidth.value}px`);
});

watch(
  () => menu.value,
  () => {
    appStore.state.adminMenu = menu.value;
  },
  { deep: true }
);

const updateMenuFromFetch = async () => {
  try {
    await nextTick();

    const url = `${appStore.state.adminUrl}/admin.php?page=plugin-manager`;
    const response = await fetch(url);

    // Generic error
    if (!response.ok) {
      return;
    }

    // Get HTML content
    const htmlContent = await response.text();

    // Create a DOM parser to handle the HTML content
    const parser = new DOMParser();
    const doc = parser.parseFromString(htmlContent, "text/html");

    // Find the metrics script tag
    const menuNode = doc.getElementById("adminmenumain");

    if (!menuNode) return;

    // Stops plugin redirection links from altering the active link
    const links = menuNode.querySelectorAll("a");
    for (let link of links) {
      link.classList.remove("current");
      link.classList.remove("wp-menu-open");
      if (link.getAttribute("aria-current") == "page") {
        link.removeAttribute("aria-current");
      }

      if (link.classList.contains("toplevel_page_plugin-manager")) {
        link.classList.add("current");
      }
    }

    const linksListItems = menuNode.querySelectorAll("li");
    for (let listItem of linksListItems) {
      listItem.classList.remove("current");
      listItem.classList.remove("wp-menu-open");
    }

    // Update menu
    const existingMenuNode = document.getElementById("adminmenumain");
    existingMenuNode.replaceWith(menuNode);
    await nextTick();
    // Process new menu
    await setMenu();
    filterMenu();
  } catch (err) {}
};

document.addEventListener("uipx-plugin-activated", updateMenuFromFetch);
document.addEventListener("uipx-plugin-deactivated", updateMenuFromFetch);

setGravatar();
getMenuForUser();
</script>

<template>
  <div
    class="flex flex-col gap-1 w-full h-full relative max-h-[100dvh]"
    ref="menupanel"
    :class="isMenuMinified ? 'py-4' : 'p-4'"
    :style="isMenuMinified ? 'width:auto' : `width:${panelWidth}px`"
    v-if="OGmenu"
  >
    <div class="flex flex-row items-center" :class="isMenuMinified ? 'place-content-center mb-3' : 'place-content-between'">
      <template v-if="!isMenuMinified">
        <a :href="appStore.state.adminUrl" class="bg-white dark:bg-zinc-800 shadow p-2 rounded-lg place-self-start" v-if="!appStore.state?.uixpress_settings?.logo">
          <AppIcon icon="uipress" class="text-2xl dark:text-white" />
        </a>

        <a :href="appStore.state.adminUrl" v-else class="h-10">
          <img :src="prefersDark && appStore.state?.uixpress_settings?.dark_logo ? appStore.state.uixpress_settings.dark_logo : appStore.state.uixpress_settings.logo" class="h-full" />
        </a>
      </template>

      <AppButton v-if="!mobile" type="transparent" :title="__('Minify menu', 'uixpress')" @click="appStore.state.menu_minimised = !appStore.state.menu_minimised" class="hidden md:block">
        <AppIcon :icon="appStore.state.menu_minimised ? 'right_panel_close' : 'left_panel_close'" class="text-xl text-zinc-500 dark:text-zinc-400" />
      </AppButton>
    </div>

    <component is="style"> {{ returnIconOverrides }}</component>
    <component is="style"> {{ returnDashIconClasses }}</component>

    <!-- Searcher -->
    <div class="relative flex mt-6" v-if="!isMenuMinified">
      <input
        v-model="search"
        class="indent-1 px-4 pl-12 py-2 border border-zinc-200 dark:border-zinc-800 rounded-lg w-full transition-all outline outline-transparent outline-offset-[-2px] focus:outline-indigo-300 dark:focus:outline-indigo-700 focus:shadow-xs text-sm bg-transparent"
        :placeholder="__('Search menu', 'uixpress')"
        @keyup.down="handleArrowDown"
        @keyup.up="handleArrowUp"
        @keyup.enter="handleKeyEnter"
      />

      <!-- Icon-->
      <div class="absolute top-0 left-0 h-full flex flex-col place-content-center px-4 py-1">
        <AppIcon icon="search" class="text-xl text-zinc-400" />
      </div>
    </div>

    <div v-if="loading" class="flex flex-col gap-6 mt-6" :class="!isMenuMinified ? 'p-3' : ''">
      <div v-for="index in 4" class="flex flex-col gap-3">
        <div class="h-6 rounded-lg bg-zinc-100 dark:bg-zinc-800 animate-pulse"></div>
        <div class="h-4 rounded-lg bg-zinc-100 dark:bg-zinc-800 animate-pulse" :class="!isMenuMinified ? 'ml-8 w-2/3' : 'w-full'"></div>
        <div class="h-4 rounded-lg bg-zinc-100 dark:bg-zinc-800 animate-pulse" :class="!isMenuMinified ? 'ml-8 w-1/4' : 'w-full'"></div>
        <div class="h-4 rounded-lg bg-zinc-100 dark:bg-zinc-800 animate-pulse" :class="!isMenuMinified ? 'ml-8 w-1/3' : 'w-full'"></div>
      </div>
    </div>

    <!-- Minimised menu -->
    <template v-if="isMenuMinified">
      <div class="relative grow flex overflow-hidden" :class="isMenuMinified ? 'px-4' : ''">
        <div class="flex flex-col grow gap-1 custom-scrollbar pb-16" :class="!favorites.length ? 'mt-6' : ''">
          <template v-for="(link, index) in returnMenuItems">
            <div v-if="link.type != 'separator'" class="relative group-parent" @mouseenter="link.hover = true" @mouseleave="link.hover = false">
              <MenuItemLink :link="link" :isActive="isActive(link)" :isFocused="isFocused(link)" class="p-2">
                <MenuIcon :link="link" />

                <div
                  v-if="returnOriginalLinkAttribute(link, 'notifications', link.notifications)"
                  class="absolute top-0 right-[-2px] text-xs bg-indigo-500 dark:bg-indigo-600/30 rounded border border-indigo-600 px-1 text-zinc-100"
                >
                  {{ returnOriginalLinkAttribute(link, "notifications", link.notifications) }}
                </div>
              </MenuItemLink>

              <!-- Sub menu -->

              <div
                v-if="link.submenu && link.submenu.length && link.hover"
                class="absolute right-0 top-0 translate-x-full transition-opacity translate-x-0"
                :target="link.settings?.open_new ? '_BLANK' : ''"
              >
                <Transition>
                  <SubMenu :parent="menupanel" :mouseenter="() => (link.hover = true)" :mouseleave="() => (link.hover = false)">
                    <template v-for="sublink in searchedSubs(link.submenu)">
                      <SubMenuItem :isActive="isActive(sublink)" :isFocused="isFocused(sublink)" :sublink="sublink" :isFavorite="isFavorite(sublink.url)" :hideFavorite="true" />
                    </template>
                  </SubMenu>
                </Transition>
              </div>
            </div>

            <div v-else class="w-full my-2"></div>
          </template>
        </div>
        <div
          v-if="returnLayout != 'bubble'"
          class="absolute bottom-0 left-0 right-0 h-32 bg-gradient-to-t from-white to-transparent dark:from-zinc-900 pointer-events-none"
          id="uixp-menu-overlay"
        ></div>
      </div>

      <div class="flex flex-row items-center gap-3 place-content-center">
        <div class="w-7 aspect-square bg-zinc-700 dark:bg-indigo-700 text-white rounded-full font-semibold flex place-content-center items-center justify-center shrink-0 relative overflow-hidden">
          <span class="uppercase">{{ appStore.state.userName.charAt(0) }}</span>
          <img v-if="gravatar" :src="gravatar" class="absolute w-full h-full z-[2]" />
        </div>
      </div>
    </template>

    <!-- Standard menu -->
    <template v-if="!isMenuMinified">
      <!-- Favorites -->
      <TransitionGroup>
        <template v-if="favorites.length && !search">
          <div v-if="favorites.length" class="text-sm py-3 font-medium mt-2 text-zinc-500 dark:text-zinc-400">{{ __("Shortcuts", "uixpress") }}</div>
          <div class="flex flex-col gap-1" v-if="favorites.length">
            <VueDraggableNext class="contents" :group="{ name: 'menus', pull: true, put: true, revertClone: false }" :list="favorites" animation="300" :sort="true">
              <TransitionGroup>
                <template v-for="(link, index) in favorites" :key="index" :index="index">
                  <div v-if="link.type != 'separator'">
                    <MenuItemLink :link="link" :isActive="false" :isFocused="false" class="p-1 pr-4 pl-1">
                      <!--Icon-->
                      <div class="absolute px-4 left-0">
                        <MenuIcon :link="link" />
                      </div>

                      <!-- Item name -->
                      <MenuItemName :link="link" />

                      <!-- Favorite icon icon -->
                      <AppIcon icon="close" class="opacity-0 group-hover:opacity-100 text-base transition-opacity" @click.prevent.stop="removeFavorite(link)" />

                      <!-- Open in new -->
                      <AppIcon v-if="link.settings?.open_new" icon="open_new" class="opacity-0 group-hover:opacity-100 text-base transition-opacity" />
                    </MenuItemLink>
                  </div>
                </template>
              </TransitionGroup>
            </VueDraggableNext>
          </div>
        </template>
      </TransitionGroup>

      <div class="border-t border-zinc-200 dark:border-zinc-800 my-6" v-if="favorites.length"></div>

      <!-- Standard expanded menu
      --
      -- Menu template for non minimised menu
      -->
      <div class="relative grow overflow-hidden flex">
        <div class="flex flex-col grow gap-1 grow overflow-auto pb-16" :class="!favorites.length ? 'mt-6' : ''">
          <template v-for="(link, index) in returnMenuItems">
            <div v-if="link.type != 'separator'" @mouseenter="link.hover = true" @mouseleave="link.hover = false" class="relative group-parent">
              <MenuItemLink :link="link" :isActive="isActive(link)" :isFocused="isFocused(link)" class="p-1 pr-4 pl-1">
                <!--Icon-->
                <div class="absolute px-4 left-0">
                  <MenuIcon :link="link" />
                </div>

                <!-- Item name -->
                <MenuItemName :link="link" />

                <!-- Favorite icon icon -->
                <AppIcon v-if="!isFavorite(link.url)" icon="star" class="opacity-0 group-hover:opacity-100 text-base transition-opacity" @click.prevent.stop="addFavorite(link)" />

                <!-- Submenu icon icon -->
                <AppIcon
                  v-if="link.submenu && link.submenu.length && appStore.state.uixpress_settings?.submenu_style != 'hover'"
                  :icon="isActive(link) || link.active ? 'expand_more' : 'chevron_left'"
                  :class="isActive(link) ? 'opacity-100' : ''"
                  class="opacity-0 group-hover:opacity-100 text-base transition-opacity"
                  @click.prevent.stop="link.active = !link.active"
                />

                <!-- Open in new -->
                <AppIcon v-if="link.settings?.open_new" icon="open_new" :class="isActive(link) ? 'opacity-100' : ''" class="opacity-0 group-hover:opacity-100 text-base transition-opacity" />
              </MenuItemLink>

              <!-- Inline Sub menu -->
              <Transition v-if="shouldShowSubMenu(link)">
                <div class="mt-2 mb-4 pl-1">
                  <template v-for="sublink in searchedSubs(link.submenu)">
                    <SubMenuItem :isActive="isActive(sublink)" :isFocused="isFocused(sublink)" :sublink="sublink" :isFavorite="isFavorite(sublink.url)" class="pl-12" />
                  </template>
                </div>
              </Transition>

              <!-- Hover submenu -->
              <div
                v-else-if="link.submenu && link.submenu.length && link.hover && appStore.state.uixpress_settings?.submenu_style == 'hover' && !mobile"
                class="absolute right-0 top-0 translate-x-full transition-opacity translate-x-0"
                :target="link.settings?.open_new ? '_BLANK' : ''"
              >
                <Transition>
                  <SubMenu :parent="menupanel" :mouseenter="() => (link.hover = true)" :mouseleave="() => (link.hover = false)">
                    <template v-for="sublink in searchedSubs(link.submenu)">
                      <SubMenuItem :isActive="isActive(sublink)" :isFocused="isFocused(sublink)" :sublink="sublink" :isFavorite="isFavorite(sublink.url)" :hideFavorite="true" />
                    </template>
                  </SubMenu>
                </Transition>
              </div>
            </div>

            <div v-else-if="link.type == 'separator' && !link.settings?.hidden && !link.settings?.name" class="w-full my-2" :id="link.id"></div>

            <div v-else-if="link.type == 'separator' && !link.settings?.hidden && link.settings?.name" class="text-sm py-3 font-medium mt-2 rounded-lg" v-html="link.settings.name" :id="link.id"></div>
          </template>
        </div>
        <div
          v-if="returnLayout != 'bubble'"
          class="absolute bottom-0 left-0 right-0 h-32 bg-gradient-to-t from-white to-transparent dark:from-zinc-900 pointer-events-none"
          id="uixp-menu-overlay"
        ></div>
      </div>

      <!-- USER DETAILS
      --
      -- Show info about the current user
      -->
      <div class="flex flex-row items-center gap-3 shrink">
        <div
          class="w-8 aspect-square bg-zinc-700 dark:bg-indigo-700 text-white rounded-full font-semibold flex place-content-center items-center justify-center shrink-0 relative overflow-hidden border border-zinc-200 dark:border-zinc-700"
        >
          <span class="lowercase relative" style="top: calc(((-1em / 1.5) + 1ex) / 2)">{{ appStore.state.userName.charAt(0) }}</span>
          <img v-if="gravatar" :src="gravatar" class="absolute w-full h-full" />
        </div>
        <div class="flex flex-col leading-tight grow min-w-0">
          <span class="truncate dark:text-zinc-100">{{ appStore.state.userName }}</span>
          <span class="text-xs text-zinc-400 truncate">{{ appStore.state.userEmail }}</span>
        </div>
      </div>

      <ResizerHandler @resize="updatePanelWidth" :minWidth="160" :panelRef="menupanel" />
    </template>
  </div>
</template>

