<script setup>
import { ref, watch, onMounted, onUnmounted, computed, nextTick, watchEffect } from "vue";
import { useRouter } from "vue-router";

// Store
import { useAppStore } from "@/store/app/app.js";
const appStore = useAppStore();

// Functions
import { lmnFetch } from "@/assets/js/functions/lmnFetch.js";
import { notify } from "@/assets/js/functions/notify.js";
import { inSearch } from "@/assets/js/functions/inSearch.js";
import { useColorScheme } from "@/assets/js/functions/useColorScheme.js";

// Comps
import AppIcon from "@/components/utility/icons/index.vue";
import AppButton from "@/components/utility/app-button/index.vue";
import Modal from "@/components/utility/modal/index.vue";
import AppInput from "@/components/utility/text-input/index.vue";

// Refs
const { userPreference, setColorScheme, prefersDark, globalOverride } = useColorScheme();
const router = useRouter();
const showModal = ref(false);
const searchQuery = ref("");
const searchmodal = ref(null);
const searchResults = ref({});
const isLoading = ref(false);
const searchInput = ref("");
const activeResultIndex = ref(-1);

// Define quick actions
const quickActions = ref([
  { id: "new-post", name: __("New Post", "uixpress"), icon: "add", action: () => newPost() },
  { id: "new-page", name: __("New Page", "uixpress"), icon: "add", action: () => newPage() },
  { id: "view-dashboard", name: __("View dashboard", "uixpress"), icon: "equalizer", action: () => (window.location.href = `${appStore.state.adminUrl}`) },
  { id: "view-site", name: __("View site", "uixpress"), icon: "home", action: () => (window.location.href = `${appStore.state.siteURL}`) },
  { id: "site-settings", name: __("Site settings", "uixpress"), icon: "tune", action: () => (window.location.href = `${appStore.state.adminUrl}options-general.php`) },
  { id: "dark-mode", name: __("Dark mode", "uixpress"), icon: "dark_mode", action: () => setColorScheme("dark"), hidden: false },
  { id: "light-mode", name: __("Light mode", "uixpress"), icon: "light_mode", action: () => setColorScheme("light"), hidden: false },
]);

// Remove site settings if user can't do that
if (!appStore.state.canManageOptions) {
  const foundIndex = quickActions.value.findIndex((item) => item.id == "site-settings");
  quickActions.value.splice(foundIndex, 1);
}

// Filter quick actions based on search query
const filteredQuickActions = computed(() => {
  if (!searchQuery.value) return quickActions.value.filter((action) => !action.hidden);
  const query = searchQuery.value.toLowerCase();
  return quickActions.value.filter((action) => action.name.toLowerCase().includes(query) && !action.hidden);
});

const flattenedResults = computed(() => {
  const results = Object.entries(searchResults.value).flatMap(([category, results]) => results.map((result) => ({ ...result, category })));
  return [...filteredQuickActions.value.map((action) => ({ ...action, category: "quickActions" })), ...results];
});

const returnCategoryName = (slug) => {
  const categories = returnEndPoints.value;
  const category = categories.find((cat) => cat.slug == slug);

  return category ? category.name : slug;
};

const returnAdminMenuLinks = computed(() => {
  let formatted = [];

  if (!Array.isArray(appStore.state.adminMenu)) return [];

  for (let parent of appStore.state.adminMenu) {
    // Not interested in seps
    if (parent.type == "separator") continue;
    if (parent?.settings?.hidden) continue;

    if (inSearch(searchQuery.value, parent?.settings?.name || parent.name, parent.url)) {
      formatted.push({ url: parent.url, name: parent?.settings?.name || parent.name, id: `${parent.url}-${parent.name}` });
    }

    if (!Array.isArray(parent.submenu)) continue;

    // Loop submenu
    for (let subitem of parent.submenu) {
      // Not interested in seps
      if (subitem.type == "separator") continue;
      if (subitem?.settings?.hidden) continue;

      // Not in search
      if (!inSearch(searchQuery.value, subitem.name || subitem?.settings?.name, subitem.url)) continue;

      formatted.push({
        url: subitem.url,
        name: `<span class="text-zinc-400 dark:text-zinc-400">${parent?.settings?.name || parent.name} ></span> ${subitem?.settings?.name || subitem.name}`,
        id: `${subitem.url}-${subitem.name}`,
      });
    }
  }

  return formatted;
});

/**
 * Fetches orders
 */
const newPost = async () => {
  window.location.href = `${appStore.state.adminUrl}post-new.php`;
};

/**
 * Fetches orders
 */
const newPage = async () => {
  window.location.href = `${appStore.state.adminUrl}post-new.php?post_type=page`;
};

const closeModal = () => {
  showModal.value = false;
  searchQuery.value = "";
  searchResults.value = {};
  activeResultIndex.value = 0;
};

const returnEndPoints = computed(() => {
  const search_post_types = appStore.state.uixpress_settings.search_post_types;

  let endpoints = [
    { name: "posts", slug: "post", endpoint: "wp/v2/posts", searchParam: "search" },
    { name: "pages", slug: "page", endpoint: "wp/v2/pages", searchParam: "search" },
  ];

  if (Array.isArray(search_post_types)) {
    if (search_post_types.length > 0) {
      endpoints.splice(0, 2);

      const formatted = search_post_types.map((item) => ({ ...item, endpoint: item.rest_base, searchParam: "search" }));

      endpoints = [...formatted, ...endpoints];
    }
  }

  endpoints.push({ name: "categories", slug: "category", endpoint: "wp/v2/categories", searchParam: "search" });

  if (appStore.state.canManageOptions) {
    endpoints.push({ name: "users", slug: "user", endpoint: "wp/v2/users", searchParam: "search" });
  }

  return endpoints;
});

const performSearch = async () => {
  if (searchQuery.value.trim() === "") return;

  if (searchQuery.value.length < 1) return;
  isLoading.value = true;
  try {
    const searchPromises = returnEndPoints.value.map(({ name, endpoint, searchParam, slug }) =>
      lmnFetch({
        endpoint,
        params: { [searchParam]: searchQuery.value, per_page: 5, context: "view", author: appStore.state.canManageOptions ? "" : appStore.state.userID },
      }).then((response) => ({ slug, data: response.data }))
    );

    const results = await Promise.all(searchPromises);

    results.forEach(({ slug, data }) => {
      searchResults.value[slug] = data;
    });
  } catch (error) {
    console.error("Error performing search:", error);
  } finally {
    isLoading.value = false;
  }
};

const debounce = (fn, delay) => {
  let timeoutId;
  return (...args) => {
    clearTimeout(timeoutId);
    timeoutId = setTimeout(() => fn(...args), delay);
  };
};

const openModal = (evt) => {
  document.activeElement.blur();

  searchmodal.value.show(evt);
  activeResultIndex.value = 0;
};

const debouncedSearch = debounce(performSearch, 300);

watch(searchQuery, (newValue, oldValue) => {
  if (newValue !== oldValue) {
    debouncedSearch();
    activeResultIndex.value = 0;
  }
});

const handleKeyDown = (event) => {
  if ((event.metaKey || event.ctrlKey) && event.key === "k") {
    event.preventDefault();
    openModal();
  }

  if (event.key === "Escape") {
    searchmodal.value.close();
  }
};

const handleArrowDown = (event) => {
  event.preventDefault();
  activeResultIndex.value = (activeResultIndex.value + 1) % flattenedResults.value.length;
};

const handleArrowUp = (event) => {
  event.preventDefault();
  activeResultIndex.value = (activeResultIndex.value - 1 + flattenedResults.value.length) % flattenedResults.value.length;
};

const handleKeyEnter = (event) => {
  event.preventDefault();
  const activeResult = flattenedResults.value[activeResultIndex.value];
  if (activeResult) {
    if (activeResult.category === "quickActions") {
      activeResult.action();
    } else {
      window.location.href = returnItemLink(activeResult.category, activeResult);
    }
    searchmodal.value.close();
  }
};

const returnItemLink = (category, item) => {
  if (category == "menu") return item.url;
  if (category == "post" || category == "page") return `${appStore.state.adminUrl}post.php?post=${item.id}&action=edit`;
  if (category == "user") return `${appStore.state.adminUrl}user-edit.php?user_id=${item.id}`;
  return `${appStore.state.adminUrl}post.php?post=${item.id}&action=edit`;
};

onMounted(() => {
  window.addEventListener("keydown", handleKeyDown);
});

onUnmounted(() => {
  window.removeEventListener("keydown", handleKeyDown);
});

watchEffect(() => {
  if (searchInput.value) {
    searchInput.value.focus();
  }
});

watchEffect(() => {
  const darkItem = quickActions.value.find((item) => item.id == "dark-mode");
  const lightItem = quickActions.value.find((item) => item.id == "light-mode");

  // Global theme is set so remove switchers
  if (globalOverride.value) {
    darkItem.hidden = true;
    lightItem.hidden = true;
    return;
  }

  if (prefersDark.value) {
    darkItem.hidden = true;
    lightItem.hidden = false;
  } else {
    darkItem.hidden = false;
    lightItem.hidden = true;
  }
});

watch(
  () => searchQuery.value,
  () => {
    searchResults.value.menu = returnAdminMenuLinks.value;
  }
);
</script>

<template>
  <AppButton type="transparent" @click.prevent="openModal" aria-label="Open search">
    <AppIcon icon="search" class="text-xl" />
  </AppButton>

  <Modal ref="searchmodal" position="top" @keydown="handleModalKeyDown">
    <div class="mx-auto p-6 w-[500px] flex flex-col gap-6 max-w-[100vw]">
      <div class="relative flex">
        <input
          class="px-2 py-2 border border-zinc-200 dark:border-zinc-700 dark:bg-transparent rounded-lg w-full transition-all outline outline-transparent outline-offset-[-2px] focus:outline-indigo-300 dark:focus:outline-indigo-700 focus:shadow-xs text-sm pl-8 dark:text-zinc-300"
          v-model="searchQuery"
          :placeholder="__('Search posts, pages, categories, users...', 'uixpress')"
          autofocus
          name="search"
          icon="search"
          @keyup.down="handleArrowDown"
          @keyup.up="handleArrowUp"
          @keyup.enter="handleKeyEnter"
          autocomplete="off"
          ref="searchInput"
        />

        <div class="absolute top-0 left-0 h-full flex flex-col place-content-center px-2 py-1">
          <AppIcon icon="search" class="text-lg text-zinc-400" />
        </div>

        <Transition>
          <div class="absolute top-0 right-0 h-full flex flex-col place-content-center px-2 py-1" v-if="isLoading">
            <svg class="animate-spin text-indigo-500 w-4" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24">
              <circle class="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" stroke-width="4"></circle>
              <path class="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path>
            </svg>
          </div>
        </Transition>
      </div>

      <div class="relative">
        <div class="flex flex-col gap-6 max-h-[70dvh] overflow-auto">
          <!-- Quick Actions -->
          <div v-if="filteredQuickActions.length">
            <div class="mb-2 capitalize text-zinc-400 dark:text-zinc-200">{{ __("Quick Actions", "uixpress") }}</div>
            <ul class="flex flex-col">
              <li
                v-for="(action, index) in filteredQuickActions"
                :key="action.id"
                :class="[
                  'p-2 rounded-lg transition-all cursor-pointer pl-3',
                  index === activeResultIndex
                    ? 'bg-zinc-100 dark:bg-zinc-800 text-zinc-950 dark:text-zinc-100'
                    : 'hover:bg-zinc-100 dark:hover:bg-zinc-800 hover:text-zinc-950 dark:text-zinc-400 dark:hover:text-zinc-100',
                ]"
              >
                <a
                  @click.prevent="
                    action.action();
                    searchmodal.close();
                  "
                  class="flex flex-row items-center gap-3"
                >
                  <AppIcon :icon="action.icon" class="text-zinc-400 text-lg" />
                  <span class="grow">{{ action.name }}</span>
                  <AppIcon icon="chevron_right" class="text-zinc-400" />
                </a>
              </li>
            </ul>
          </div>

          <Transition mode="out-in" name="slide-up">
            <!-- Existing search results -->

            <div v-if="searchResults" class="flex flex-col gap-6">
              <template v-for="(results, category) in searchResults" :key="category">
                <div v-if="results.length">
                  <div class="mb-2 capitalize text-zinc-400 dark:text-zinc-200">{{ returnCategoryName(category) }}</div>
                  <ul class="flex flex-col">
                    <li
                      v-for="(result, index) in results"
                      :key="result.id"
                      :class="[
                        'p-2 rounded-lg transition-all cursor-pointer pl-3',
                        flattenedResults.findIndex((r) => r.id === result.id && r.category === category) === activeResultIndex
                          ? 'bg-zinc-100 dark:bg-zinc-800 text-zinc-950 dark:text-zinc-100'
                          : 'hover:bg-zinc-100 dark:hover:bg-zinc-800 hover:text-zinc-950 dark:text-zinc-400 dark:hover:text-zinc-100',
                      ]"
                    >
                      <a :href="returnItemLink(category, result)" class="flex flex-row items-center gap-3" @click="searchmodal.close()">
                        <div
                          v-if="category == 'user'"
                          class="w-5 aspect-square bg-zinc-700 dark:bg-indigo-700 text-white rounded-full font-semibold flex place-content-center items-center justify-center shrink-0 relative overflow-hidden"
                        >
                          <span class="uppercase text-sm">{{ result.slug.charAt(0) }}</span>
                        </div>

                        <div
                          v-if="category == 'attachment'"
                          class="w-5 aspect-square bg-zinc-200 dark:bg-zinc-700 rounded font-semibold flex place-content-center items-center justify-center shrink-0 relative overflow-hidden overflow-hidden"
                        >
                          <img v-if="result.mime_type.includes('image')" :src="result.source_url" class="w-5 h-5 object-contain object-center" />
                          <AppIcon v-else icon="description" />
                        </div>

                        <span class="grow truncate" v-html="result.name || result.title?.rendered || result.email"></span>
                        <AppIcon icon="chevron_right" class="text-zinc-400" />
                      </a>
                    </li>
                  </ul>
                </div>
              </template>
            </div>
          </Transition>
        </div>
        <div class="absolute bottom-0 left-0 right-0 h-10 bg-gradient-to-t from-white dark:from-zinc-900 to-transparent pointer-events-none" v-if="!isLoading && flattenedResults.length > 20" />
      </div>

      <div class="flex flex-row gap-6 items-center text-zinc-500">
        <!--Arrows-->
        <div class="flex flex-row gap-2 items-center">
          <span class="p-1 border border-zinc-200 dark:border-zinc-700 rounded-lg"><AppIcon icon="arrow_up" /></span>
          <span class="p-1 border border-zinc-200 dark:border-zinc-700 rounded-lg"><AppIcon icon="arrow_down" /></span>

          <span class="text-sm">{{ __("to navigate", "uixpress") }}</span>
        </div>

        <!--Arrows-->
        <div class="flex flex-row gap-2 items-center">
          <span class="p-1 border border-zinc-200 dark:border-zinc-700 rounded-lg"><AppIcon icon="return" /></span>

          <span class="text-sm">{{ __("to select", "uixpress") }}</span>
        </div>

        <!--Arrows-->
        <div class="flex flex-row gap-2 items-center">
          <span class="p-1 border border-zinc-200 dark:border-zinc-700 rounded-lg text-xs">Esc</span>

          <span class="text-sm">{{ __("to close", "uixpress") }}</span>
        </div>
      </div>
    </div>
  </Modal>
</template>

<style scoped>
.slide-up-enter-active,
.slide-up-leave-active {
  transition: all 0.2s ease-out;
}

.slide-up-enter-from,
.slide-up-leave-to {
  opacity: 0;
}

.slide-up-enter-to,
.slide-up-leave-from {
  opacity: 1;
}
</style>
