<script setup>
import { ref, computed, watch, watchEffect, defineProps } from "vue";

import { useRoute } from "vue-router";
import { encodeToHash } from "@/assets/js/functions/encodeToHash.js";

// Comps
import AppIcon from "@/components/utility/icons/index.vue";
import AppButton from "@/components/utility/app-button/index.vue";
import Notifications from "@/components/app/notifications/index.vue";
import AppSearch from "@/components/app/search/index.vue";
import AppMenu from "@/components/app/menu/index.vue";
import ContextMenu from "@/components/utility/context-menu/index.vue";
import AppCheckbox from "@/components/utility/checkbox-input/index.vue";
import WpToolBar from "@/components/app/wp-toolbar/index.vue";
import WpNotifications from "@/components/app/wp-notifications/index.vue";
import { useColorScheme } from "@/assets/js/functions/useColorScheme.js";
import { lmnFetch } from "@/assets/js/functions/lmnFetch.js";

// Store
import { useAppStore } from "@/store/app/app.js";
const props = defineProps(["frontend"]);
const appStore = useAppStore();
const menuopen = ref(false);
const gravatar = ref(false);
const contextmenu = ref(null);
const screenOptions = ref(false);
const screenHelp = ref(false);
const loading = ref(false);
const helpVisible = ref(true);
const optionsVisible = ref(true);
const toolbarContainer = ref(null);
const { userPreference, setColorScheme, globalOverride, prefersDark } = useColorScheme();

const toggleFullScreen = () => {
  if (appStore.state.fullScreen) appStore.updateState("fullScreen", false);
  else appStore.updateState("fullScreen", true);
};

// Methods
const setGravatar = async () => {
  const hashHex = await encodeToHash(appStore.state.userEmail);
  gravatar.value = `https://gravatar.com/avatar/${hashHex}?d=blank`;
};

const breadCrumbs = computed(() => {
  return route.matched.map((subroute) => ({
    path: subroute.path,
    name: typeof subroute.meta.name == "function" ? subroute.meta.name(route) : subroute.meta.name,
  }));
});

/**
 * Fetches orders
 */
const logoutUser = async () => {
  loading.value = true;

  const args = { endpoint: "wc/v3/logout", params: {}, type: "POST" };
  const data = await lmnFetch(args);

  loading.value = false;

  window.location = appStore.state.loginUrl;
};

const toggleScreenLinks = () => {
  const wrap = document.querySelector("#screen-options-wrap");
  const meta = document.querySelector("#screen-meta");

  // No screen links so bail
  if (!wrap || !meta) return;

  if (!screenOptions.value) {
    wrap.style.display = "block";
    meta.style.display = "block";
    wrap.classList.remove("hidden");
    screenOptions.value = true;
  } else {
    wrap.style.display = "none";
    meta.style.display = "none";
    screenOptions.value = false;
  }
};

const toggleHelpLinks = () => {
  const wrap = document.querySelector("#contextual-help-wrap");
  const meta = document.querySelector("#screen-meta");

  // No screen links so bail
  if (!wrap || !meta) return;

  if (!screenHelp.value) {
    wrap.style.display = "block";
    meta.style.display = "block";
    wrap.classList.remove("hidden");
    screenHelp.value = true;
  } else {
    wrap.style.display = "none";
    meta.style.display = "none";
    screenHelp.value = false;
  }
};

const setScreenActions = () => {
  const help = document.querySelector("#contextual-help-wrap");
  const options = document.querySelector("#screen-options-wrap");

  if (!help) helpVisible.value = false;
  if (!options) optionsVisible.value = false;
};

watchEffect(() => {
  if (toolbarContainer.value) {
    const rect = toolbarContainer.value.getBoundingClientRect();
    document.documentElement.style.setProperty("--uixp-toolbar-height", `${rect.height}px`);
  }
});

setScreenActions();
setGravatar();
</script>

<template>
  <div id="uixp-toolbar"
    class="px-6 flex flex-row items-center pointer-events-auto max-w-full gap-6 max-lg:overflow-hidden"
    ref="toolbarContainer" :class="frontend ? '' : 'py-3'">
    <component is="style" v-if="frontend"> #uixp-toolbar { height: 32px; } @media screen and (max-width: 782px) {
      #uixp-toolbar { height: 46px; } } </component>

    <div class="flex flex-row items-center gap-4 hidden max-lg:flex" v-if="!frontend">
      <AppButton type="transparent" :title="__('Toggle menu', 'uixpress')" @click="menuopen = !menuopen">
        <AppIcon icon="menu" class="text-xl" />
      </AppButton>

      <Transition>
        <div class="fixed top-0 left-0 right-0 bottom-0 h-dvh w-dvh z-[9999999] bg-zinc-900/10 flex flex-row"
          v-if="menuopen" @click="menuopen = false">
          <div
            class="bg-white dark:bg-zinc-900 w-auto max-w-full shadow-lg border-r border-zinc-200 dark:border-zinc-700 h-full relative"
            @click.stop>
            <AppMenu :mobile="true" />

            <div class="absolute top-0 right-0 p-4">
              <AppButton type="transparent" :title="__('Toggle menu', 'uixpress')" @click="menuopen = false">
                <AppIcon icon="close" class="text-xl" />
              </AppButton>
            </div>
          </div>
        </div>
      </Transition>
    </div>

    <div class="relative grow max-lg:overflow-hidden flex flex-row items-center">
      <a v-if="appStore.state?.uixpress_settings?.logo && frontend" :href="appStore.state.adminUrl" class="h-5 mr-6">
        <img
          :src="prefersDark && appStore.state?.uixpress_settings?.dark_logo ? appStore.state.uixpress_settings.dark_logo : appStore.state.uixpress_settings.logo"
          class="h-full" />
      </a>

      <a v-else-if="frontend" :href="appStore.state.adminUrl" class="mr-6">
        <AppIcon icon="uipress" class="text-xl dark:text-white" />
      </a>

      <div class="flex flex-row items-center gap-4 w-full max-lg:overflow-auto no-scrollbar pr-16">
        <WpToolBar />
      </div>
      <div
        class="hidden max-lg:block absolute right-0 w-16 top-0 bottom-0 bg-gradient-to-l from-white to-transparent dark:from-zinc-900 pointer-events-none"
        id="uixp-toolbar-overlay"></div>
    </div>

    <div class="flex flex-row items-center gap-1">
      <AppSearch v-if="!appStore.state?.uixpress_settings?.disable_search" />

      <AppButton type="transparent" :title="__('Screen options', 'uixpress')" @click="toggleScreenLinks"
        v-if="optionsVisible && !appStore.state?.uixpress_settings?.hide_screenoptions" class="max-md:hidden">
        <AppIcon icon="tune" class="text-xl" />
      </AppButton>

      <AppButton type="transparent" :title="__('Help', 'uixpress')" @click="toggleHelpLinks"
        v-if="helpVisible && !appStore.state?.uixpress_settings?.hide_help_toggle" class="max-md:hidden">
        <AppIcon icon="help_alt" class="text-2xl" />
      </AppButton>

      <a :href="appStore.state.siteURL" v-if="!frontend">
        <AppButton type="transparent" :title="__('View site', 'uixpress')">
          <AppIcon icon="home" class="text-xl" />
        </AppButton>
      </a>

      <a :href="appStore.state.adminUrl" v-else-if="frontend">
        <AppButton type="transparent" :title="__('View dashboard', 'uixpress')">
          <AppIcon icon="dashboard" class="text-xl" />
        </AppButton>
      </a>

      <WpNotifications v-if="!frontend" />

      <div
        class="w-6 aspect-square bg-zinc-700 dark:bg-indigo-700 text-white rounded-full font-semibold flex place-content-center items-center justify-center shrink-0 relative overflow-hidden ml-2 cursor-pointer dark:hover:bg-indigo-800 hover:bg-zinc-900 transition-colors border border-zinc-200 dark:border-zinc-700"
        @click="contextmenu.show">
        <span class="lowercase relative text-sm" style="top: calc(((-1em / 1.5) + 1ex) / 2)">{{
          appStore.state.userName.charAt(0) }}</span>
        <img v-if="gravatar" :src="gravatar" class="absolute w-full h-full" />
      </div>

      <!-- User menu -->
      <ContextMenu ref="contextmenu" :teleport="true">
        <div class="flex flex-col gap-2 w-40">
          <template v-if="!globalOverride">
            <div class="">{{ __("Theme", "uixpress") }}</div>
            <div class="flex flex-col gap-2">
              <label class="flex flex-row items-center gap-2 px-2 mt-2 cursor-pointer"
                @click.stop.prevent="setColorScheme('system')">
                <AppCheckbox :dynamic="userPreference == 'system' ? true : false" />
                <span class="dark:text-zinc-400">{{ __("System", "uixpress") }}</span>
              </label>

              <label class="flex flex-row items-center gap-2 px-2 cursor-pointer"
                @click.stop.prevent="setColorScheme('light')">
                <AppCheckbox :dynamic="userPreference == 'light' ? true : false" />
                <span class="dark:text-zinc-400">{{ __("Light", "uixpress") }}</span>
              </label>

              <label class="flex flex-row items-center gap-2 px-2 cursor-pointer"
                @click.stop.prevent="setColorScheme('dark')">
                <AppCheckbox :dynamic="userPreference == 'dark' ? true : false" />
                <span class="dark:text-zinc-400">{{ __("Dark", "uixpress") }}</span>
              </label>
            </div>

            <div class="my-2 mb-1 border-t border-zinc-200 dark:border-zinc-700"></div>
          </template>

          <AppButton type="transparent" class="shrink-0" :loading="loading" @click="logoutUser"
            :title="__('Logout', 'uixpress')">
            <span class="flex flex-row items-center gap-2 place-content-between">
              <span>{{ __("Logout", "uixpress") }}</span>
              <AppIcon icon="logout" class="text-lg" />
            </span>
          </AppButton>

        </div>
      </ContextMenu>
    </div>
  </div>
</template>

<style scoped>
/* Hide scrollbar for Chrome, Safari and Opera */
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

/* Hide scrollbar for IE, Edge and Firefox */
.no-scrollbar {
  -ms-overflow-style: none;
  /* IE and Edge */
  scrollbar-width: none;
  /* Firefox */
}
</style>
