<script setup>
import { ref, watchEffect, nextTick, onMounted } from "vue";
// Comps
import AppIcon from "@/components/utility/icons/index.vue";
import AppButton from "@/components/utility/app-button/index.vue";
import ResizerHandler from "@/components/app/panel-resizer/index.vue";

const notificationsCount = ref(0);
const notificationList = ref(null);
const open = ref(false);
const parentOpen = ref(false);
const panel = ref(null);
const notificationNodes = ref(null);
const mounted = ref(false);
const isJiggling = ref(false);
const notificationsList = ref(null);
const panelWidth = ref(800);
const targetSelector = ".notice:not(table .notice):not(#message.notice):not(.hide-if-js .notice):not(.themes .notice):not(.notice.hidden):not(.notice[aria-hidden='true']):not(.notice.hide-if-js)";

const getNotificationsCount = () => {
  const notis = document.querySelectorAll(targetSelector);

  // No notifications
  if (!notis) {
    notificationsCount.value = 0;
    return;
  }

  notificationsCount.value = 0;
  for (let item of [...notis]) {
    if (!item.innerText || item.id == "setting-error-settings_updated" || !isVisible(item)) continue;
    notificationsCount.value++;
  }

  if (notificationsCount.value > 0) {
    isJiggling.value = true;
    setTimeout(() => {
      isJiggling.value = false;
    }, 500);
  }
};

const isVisible = (element) => {
  const style = window.getComputedStyle(element);
  return style.display !== "none" && style.visibility !== "hidden";
};

const setNotifications = () => {
  if (!notificationList.value || notificationNodes.value) return;

  notificationNodes.value = document.querySelectorAll(targetSelector);

  if (!notificationNodes.value) return;

  for (let item of [...notificationNodes.value]) {
    if (!item.innerText || item.id == "setting-error-settings_updated") continue;
    notificationList.value.appendChild(item);
  }
};

const close = (evt) => {
  if (!panel.value) return;
  // Don't do anything if the click came from inside panel
  if (panel.value.contains(evt.target)) return;

  open.value = false;
};

const forceClose = (evt) => {
  open.value = false;
};

const show = () => {
  setNotifications();
  open.value = true;
};

/**
 * Updates the width of the panel.
 * @param {number} newWidth - The new width to set for the panel.
 */
const updatePanelWidth = (newWidth) => {
  panelWidth.value = newWidth;
};

watchEffect(() => {
  if (open.value) nextTick(() => (parentOpen.value = true));
  if (!open.value) nextTick(() => (parentOpen.value = false));
});

onMounted(() => {
  mounted.value = true;
  getNotificationsCount();
});
</script>

<template>
  <AppButton type="transparent" :title="__('Notifications', 'uixpress')" class="max-md:hidden" @click="show()">
    <div class="relative" :class="{ 'animate-jiggle': isJiggling }">
      <AppIcon icon="notifications-2" class="text-2xl" />

      <Transition>
        <span
          v-if="notificationsCount > 0"
          class="absolute top-0 right-0 inline-flex items-center justify-center text-xs font-bold leading-none text-rose-100 transform right-0 top-0 bg-rose-600 dark:bg-rose-700 rounded-full aspect-square h-2"
        >
        </span>
      </Transition>
    </div>
  </AppButton>

  <Teleport to="body" v-if="mounted">
    <div
      class="fixed top-0 left-0 right-0 bottom-0 bg-zinc-900/20 dark:bg-zinc-900/60 flex flex-row place-content-end z-[99999] max-w-screen"
      @click="close"
      :style="open ? '' : 'display:none!important;'"
    >
      <Transition
        enter-active-class="transition duration-200 ease-out"
        enter-from-class="transform translate-x-full"
        enter-to-class="transform translate-x-0"
        leave-active-class="transition duration-300 ease-in"
        leave-from-class="transform translate-x-0"
        leave-to-class="transform translate-x-full"
      >
        <div
          v-show="parentOpen"
          ref="panel"
          class="h-screen max-h-screen overflow-auto bg-white dark:bg-zinc-900 border-l border-zinc-200 dark:border-zinc-700 max-w-screen relative shadow-lg rounded-tl-lg rounded-bl-lg relative dark:shadow-zinc-950 box-border"
        >
          <div class="flex flex-col gap-6 p-8 max-w-full" id="uixp-notification-list" ref="notificationsList" :style="`width:${panelWidth}px`">
            <ResizerHandler @resize="updatePanelWidth" :minWidth="160" :panelRef="notificationsList" storageKey="uixp-notification-width" position="left" />
            <div class="flex flex-row items-center place-content-between">
              <div class="text-2xl font-semibold text-zinc-900 dark:text-zinc-100">{{ __("Notifications", "uixpress") }}</div>

              <div
                class="hover:bg-zinc-100 dark:hover:bg-zinc-800 p-1 rounded-lg cursor-pointer text-zinc-500 dark:text-zinc-400 hover:text-zinc-900 dark:hover:text-zinc-100 transition-all"
                :title="__('Close', 'uixpress')"
                @click="forceClose()"
              >
                <AppIcon icon="close" class="text-xl" />
              </div>
            </div>
            <div v-if="!notificationsCount" class="leading-loose p-2 rounded-lg bg-emerald-100 dark:bg-emerald-800 border border-solid border-emerald-200 dark:border-emerald-700 dark:text-zinc-300">
              {{ __("No notifications", "uixpress") }}
            </div>
            <div class="flex flex-col gap-3" ref="notificationList"></div>
          </div>
        </div>
      </Transition>
    </div>
  </Teleport>
</template>

<style>
#uixp-body .notice:not(table .notice):not(#message.notice):not(.hide-if-js .notice):not(.themes .notice):not(#setting-error-settings_updated) {
  display: none !important;
}
#uixp-notification-list .notice {
  margin: 0 !important;
}
@keyframes jiggle {
  0% {
    transform: rotate(0deg);
  }
  25% {
    transform: rotate(-8deg);
  }
  50% {
    transform: rotate(7deg);
  }
  75% {
    transform: rotate(-8deg);
  }
  100% {
    transform: rotate(0deg);
  }
}

.animate-jiggle {
  animation: jiggle 0.5s ease-in-out;
}
</style>
