<script setup>
import { defineProps } from "vue";
import { returnOriginalLinkAttribute } from "./returnOriginalLinkAttribute.js";
import AppIcon from "@/components/utility/icons/index.vue";

const props = defineProps(["sublink", "isFocused", "isActive", "isFavorite", "hideFavorite"]);

// Helps trigger events on original nodes
const triggerBaseNode = (evt) => {
  if (!props.sublink.og_node) return;

  const event = new MouseEvent("mousedown", {
    bubbles: true,
    cancelable: true,
    view: window,
  });
  props.sublink.og_node.dispatchEvent(event);
};
</script>

<template>
  <a
    :href="sublink.url"
    :id="sublink.sub_id"
    class="flex flex-row p-1 px-4 rounded-lg items-center cursor-pointer gap-1 group transition-all"
    :class="isActive || isFocused ? 'text-zinc-900 dark:text-white active font-semibold' : ' hover:bg-zinc-100 hover:dark:bg-zinc-800'"
    @click="triggerBaseNode($event)"
  >
    <div class="group-hover:text-zinc-950 dark:group-hover:text-zinc-100 transition-color flex-grow text-base grow truncate flex flex-row items-center gap-4">
      <div v-html="sublink.settings?.name || returnOriginalLinkAttribute(sublink, 'name', sublink.name)" class="truncate"></div>
      <div v-if="returnOriginalLinkAttribute(sublink, 'notifications', sublink.notifications)" class="text-xs bg-indigo-500 dark:bg-indigo-600/30 rounded border border-indigo-600 px-1 text-zinc-100">
        {{ returnOriginalLinkAttribute(sublink, "notifications", sublink.notifications) }}
      </div>
    </div>

    <!-- Favorite icon icon -->
    <AppIcon
      v-if="!isFavorite && !hideFavorite"
      icon="star"
      class="opacity-0 group-hover:opacity-100 text-base transition-opacity"
      @click.prevent.stop="addFavorite({ ...sublink, imageClasses: link.imageClasses, iconStyles: link.iconStyles })"
    />

    <!-- Open in new -->
    <AppIcon v-if="sublink.settings?.open_new" icon="open_new" :class="isActive || isFocused ? 'opacity-100' : ''" class="opacity-0 group-hover:opacity-100 text-base transition-opacity" />
  </a>
</template>
